<template>
  <div id="Hi">
    <p>Capcom BAD {{ hhw }}</p>
    <button @click="te">API</button>
    <button @click="HHW">HHW</button>
    <div class="ap"></div>
    <input :type="typename" :value="hhw" @click="hhw++" />
    <div class="mt1rem">
    <button type="button" class="btn btn-outline-primary" @click="monjiplus(0)">Primary</button>
    <button type="button" class="btn btn-outline-secondary" @click="monjiplus(1)">Secondary</button>
    <button type="button" class="btn btn-outline-success" @click="monjiplus(2)">Success</button>
    <button type="button" class="btn btn-outline-danger" @click="monjiplus(3)">Danger</button>
    <button type="button" class="btn btn-outline-warning" @click="monjiplus(4)">Warning</button>
    <button type="button" class="btn btn-outline-info" @click="monjiplus(5)">Info</button>
    <button type="button" class="btn btn-outline-light" @click="allmonji">Light</button>
    <p class="mt1rem">{{newmonji}}</p>
    </div>
  </div>
</template>
<style>
button {
  margin: 0 0.5rem;
}
.ap {
  margin: 0.5rem 0;
}
.mt1rem{
    margin-top: 1rem;
}
</style>
<script>
export default {
  el: "#Hi",
  data() {
    return {
      hhw: "100",
      typename: "submit",
      monji: ["ウ", "サ", "ダ", "ぺ", "こ", "ら"],
      newmonji:[],
    };
  },
  methods: {
    te() {
      let na = "きtら";
      const endpoint =
        "https://tcgbusfs.blob.core.windows.net/dotapp/youbike/v2/youbike_immediate.json";
      $.ajax({
        type: "GET",
        url: endpoint,
        dataType: "json",
        success: function (response) {
          const charge = [];
          charge.push(...response);
          console.log(charge[360], na);
          $(".ap").html(charge[360].sna + charge[360].updateTime);
        },
        error: function (thrownError) {
          console.log(thrownError);
        },
      });
    },
    HHW() {
      console.log(this.hhw)
    },
    monjiplus(number){
        console.log(...this.monji)
        this.newmonji.push(...this.monji[number])
    },
    allmonji(){
        this.monji.forEach(element => {
            this.newmonji.push(element)
        });
    },
  },
};
</script>